import { getValidDateOrStringDateFromValue } from "@rp/common/utils"
import { DATE_OPTIONS } from "@rp/common/utils"
import { WorkflowDescriptionResult } from "@rp/react/tanstack-api/workflow"

const getWorkflowDefaultDateFilter = (workflow: WorkflowDescriptionResult) => {
  return {
    ...DATE_OPTIONS[workflow.defaultDateFilter],
    end: getValidDateOrStringDateFromValue(DATE_OPTIONS[workflow.defaultDateFilter].end()),
    start: getValidDateOrStringDateFromValue(DATE_OPTIONS[workflow.defaultDateFilter].start()),
  }
}

export { getWorkflowDefaultDateFilter }
